import React from 'react';
import './App.css';
import ProductCard from './ProductCard.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import { HashRouter as Switch, Link, Route, HashRouter } from 'react-router-dom';
import About from './About.js';
import { Nav, Navbar, NavLink } from 'react-bootstrap/';
import Team from './Team.js';
import Home from './Home.js';
import Contact from './Contact.js';

function App(){

  return (
    <div>
      <HashRouter basename='/'>
        <div>
        <Navbar bg="dark" fixed="top" variant="dark">
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav>
              <NavLink as={Link} href="/pacelab">Pacelab ACE</NavLink>
              <NavLink as={Link} href="/about">About Our Company</NavLink>
              <NavLink as={Link} href="/team">Team</NavLink>
              <Navbar.Brand href="/" class="nav-justified">PACE America</Navbar.Brand>
              <NavLink as={Link} href="/contact">Contact Us</NavLink>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <Switch>
          <Route exact path={process.env.PUBLIC_URL + "/pacelab"} component={ProductCard} />
          <Route exact path={process.env.PUBLIC_URL + "/about"} component={About} />
          <Route exact path={process.env.PUBLIC_URL + "/team"} component={Team} />
          <Route exact path={process.env.PUBLIC_URL + "/"} component={Home} />
          <Route exact path={process.env.PUBLIC_URL + "/contact"} component={Contact} />
        </Switch>
        </div>
      </HashRouter>
    </div>
  );
}

export default App;